import React, { useState } from 'react';
import { X } from 'lucide-react';

const RequestDetailsModal = ({ isOpen, onClose, caseData, entityType, entityName, isProfileRequest = false }) => {
  const [requestType, setRequestType] = useState(isProfileRequest ? 'Profile Information' : 'Case Details');
  const [message, setMessage] = useState('');

  if (!isOpen) return null;

  const handleSubmit = (e) => {
    e.preventDefault();
    // Handle form submission here
    console.log('Request submitted:', {
      requestType,
      caseData,
      entityType,
      entityName,
      message,
      isProfileRequest
    });
    onClose();
  };

  const handleCancel = () => {
    setRequestType(isProfileRequest ? 'Profile Information' : 'Case Details');
    setMessage('');
    onClose();
  };

  // Get request type options based on whether it's a profile or case request
  const getRequestTypeOptions = () => {
    if (isProfileRequest) {
      return [
        { value: 'Profile Information', label: 'Profile Information' },
        { value: 'Contact Details', label: 'Contact Details' },
        { value: 'Financial Information', label: 'Financial Information' },
        { value: 'Legal History', label: 'Legal History' },
        { value: 'Management Details', label: 'Management Details' },
        { value: 'Other', label: 'Other' }
      ];
    } else {
      return [
        { value: 'Case Details', label: 'Case Details' },
        { value: 'Legal Documents', label: 'Legal Documents' },
        { value: 'Court Records', label: 'Court Records' },
        { value: 'Financial Information', label: 'Financial Information' },
        { value: 'Other', label: 'Other' }
      ];
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 w-full max-w-md mx-4">
        {/* Header */}
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-xl font-semibold text-gray-900">
            {isProfileRequest ? 'Request Profile Information' : 'Request Case Details'}
          </h2>
          <button
            onClick={handleCancel}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <X className="h-5 w-5" />
          </button>
        </div>

        <form onSubmit={handleSubmit}>
          {/* Request Type */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Request Type
            </label>
            <select
              value={requestType}
              onChange={(e) => setRequestType(e.target.value)}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            >
              {getRequestTypeOptions().map((option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>

          {/* Information Section */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              {isProfileRequest ? 'Profile Information' : 'Case Information'}
            </label>
            <div className="bg-blue-50 rounded-lg p-4">
              <div className="space-y-2 text-sm">
                {isProfileRequest ? (
                  <>
                    <div>
                      <span className="font-semibold text-gray-900">{entityType}:</span> {entityName || 'N/A'}
                    </div>
                    <div>
                      <span className="font-semibold text-gray-900">Request Type:</span> {requestType}
                    </div>
                  </>
                ) : (
                  <>
                    <div>
                      <span className="font-semibold text-gray-900">Case:</span> {caseData?.suit_reference_number || 'N/A'}
                    </div>
                    <div>
                      <span className="font-semibold text-gray-900">Title:</span> {caseData?.title || 'N/A'}
                    </div>
                    <div>
                      <span className="font-semibold text-gray-900">{entityType}:</span> {entityName || 'N/A'}
                    </div>
                  </>
                )}
              </div>
            </div>
          </div>

          {/* Additional Message */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Additional Message (Optional)
            </label>
            <textarea
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              placeholder="Please specify any additional information you need..."
              rows={4}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 resize-none"
            />
          </div>

          {/* Action Buttons */}
          <div className="flex items-center justify-end space-x-3">
            <button
              type="button"
              onClick={handleCancel}
              className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
            >
              Cancel
            </button>
            <button
              type="submit"
              className="px-4 py-2 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700 transition-colors"
            >
              Submit Request
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default RequestDetailsModal;
